import styles from 'components/Footer/Footer.module.scss'

// photo
import Kelly from 'assets/photos/Kelly.jpg'
import Chc from 'assets/photos/CH Chu.jpg'
import Kevin from 'assets/photos/Kevin_L.png'
import Ywj from 'assets/photos/YWJ.png'
import AvatarToolTip from 'components/ToolTips/AvatarToolTip'

export default function Footer() {
  const contributors = [
    {
      name: 'Ya-Yun',
      link: 'https://github.com/magic9701',
      image: Kelly,
      title: 'Front-End'
    },
    {
      name: 'Willy',
      link: 'https://github.com/ywcheng1207',
      image: Ywj,
      title: 'Front-End'
    },
    {
      name: 'Chia-Hsuan',
      link: 'https://github.com/jiasyuanchu',
      image: Chc,
      title: 'Back-End'
    },
    {
      name: 'Kevin',
      link: 'https://github.com/av124773',
      image: Kevin,
      title: 'Back-End'
    }
  ]

  return (
    <div className={styles.footerContainer}>
      <div className={styles.innerContainer}>
        <div className={styles.sectionOne}>
          <p> 2023  All rights reserved</p>
          <p>
            {' '}
            <a href="https://hiking.biji.co/"></a>
          </p>
        </div>
        <div className={styles.contributors}>
          <p></p>
          <div className={styles.socialLinkContainer}>
            {contributors.map((contributor) => (
              <a
                href={contributor.link}
                className={styles.avatarContainer}
                key={contributor.name}
              >
                <img src={contributor.image} alt={contributor.name} />
                <AvatarToolTip contributor={contributor} />
              </a>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}
