import { useState } from 'react'
import Toast from 'utils/sweetAlertConfig.js'
// styles
import styles from 'components/Modal/ReportModal.module.scss'
// svg
import xmark from 'assets/icons/icon-xmark.svg'
// components
import Button from 'components/Button/Button'
// api
import { addReport } from 'api/post'

// Statemodal
// const [ isReportModalOpen, setIsReportModalOpen] = useState(false)

export default function ReportPostModal({
  isReportModalOpen,
  setIsReportModalOpen,
  postId
}) {
  const [category, setCategory] = useState('')
  const [content, setContent] = useState('')

  // 
  const confirmFunction = async () => {
    if (content.trim().length === 0) {
      // 
      Toast.fire({
        icon: 'info',
        title: '!'
      })
    } else {
      const { success, message } = await addReport({
        postId,
        category,
        content
      })
      if (success) {
        setIsReportModalOpen(false)
        Toast.fire({
          icon: 'success',
          title: '!'
        })
      } else if (message === 'Error: You already reported this post!') {
        Toast.fire({
          icon: 'error',
          title: '!'
        })
      }
    }
  }
  // Description
  const handleValueChange = (event) => {
    setContent(event.target.value)
  }
  // category
  const handleCategoryChange = (event) => {
    setCategory(event.target.value)
  }
  // ICON
  const handleClose = () => {
    setIsReportModalOpen(false)
  }

  // ()
  if (!isReportModalOpen) {
    return null
  }

  return (
    <div className="modalBackground">
      <div className={styles.modalContainer}>
        <div
          className={`${styles.closeContainer} cursor-point`}
          onClick={handleClose}
        >
          <img src={xmark} alt="close" onClick={handleClose} />
        </div>
        <h4></h4>
        <div className={styles.category}>
          <label className={styles.title}></label>
          <div>
            <input
              type="radio"
              id=""
              value=""
              name="category"
              onChange={handleCategoryChange}
            />
            <label htmlFor=""></label>
            <input
              type="radio"
              id=""
              value=""
              name="category"
              onChange={handleCategoryChange}
            />
            <label htmlFor=""></label>
            <input
              type="radio"
              id=""
              value=""
              name="category"
              onChange={handleCategoryChange}
            />
            <label htmlFor=""></label>
            <input
              type="radio"
              id=""
              value=""
              name="category"
              onChange={handleCategoryChange}
            />
            <label htmlFor=""></label>
          </div>
        </div>
        <div className={styles.inputGroup}>
          <label htmlFor="content" className={styles.title}>
            
          </label>
          <textarea
            id="content"
            value={content}
            placeholder="100"
            maxLength="100"
            onChange={handleValueChange}
          />
        </div>
        <div className={styles.alertMessageContainer}>
          {content.length === 100 && (
            <div className={styles.alertMessage}>100!</div>
          )}
        </div>
        <div className={styles.outerButtonContainer}>
          <div className={styles.buttonContainer}>
            <Button
              style="secondaryButtonBright"
              text=""
              onClick={confirmFunction}
            />
          </div>
        </div>
      </div>
    </div>
  )
}
