import styles from 'components/TrailsInformation/InformationTable.module.scss'
import TableDataRow from './TableDataRow'

export default function InformationTable({ data }) {
  return (
    <div className={styles.tableContainer}>
      <table className={styles.tableOne}>
        <tbody>
          <TableDataRow label="" value={data.location} />
          <TableDataRow label="" value={data.trailType} />
          <TableDataRow label="" value={data.altitude} />
          <TableDataRow label="" value={data.trailCondition} />
          <TableDataRow label="" value={data.difficulty} />
          <TableDataRow label="" value={data.permitRequiredForEntry} />
        </tbody>
      </table>
      <table className={styles.tableTwo}>
        <tbody>
          <TableDataRow label="" value={data.distance} />
          <TableDataRow label="" value={data.trailFormat} />
          <TableDataRow label="" value={data.heightDiff} />
          <TableDataRow label="" value={data.duration} />
          <TableDataRow label="" value={data.parkOwnership} />
          <TableDataRow
            label=""
            value={data.permitRequiredForParkAccess}
          />
        </tbody>
      </table>
    </div>
  )
}
