import styles from 'components/Tag/Tag.module.scss'
import { useEffect, useState } from 'react'

export function BlackTag({ children }) {
  return <div className={styles.BlackTag}>{children}</div>
}

export function ColorTag({ children }) {
  const [style, setStyle] = useState('')
  useEffect(() => {
    if (children === '') {
      setStyle('baiyue')
    } else if (children === '') {
      setStyle('subur')
    } else if (children === '') {
      setStyle('oversea')
    } else {
      setStyle('gray')
    }
  }, [children])
  return (
    <div className={`${styles[style]} ${styles.basicOval}`}>{children}</div>
  )
}

export function DifficultyTag({ children }) {
  const [style, setStyle] = useState('')
  useEffect(() => {
    if (children === '') {
      setStyle('defLow')
    } else if (children === '-') {
      setStyle('defLowMid')
    } else if (children === '') {
      setStyle('defMid')
    } else if (children === '-') {
      setStyle('defMidHigh')
    } else if (children === '') {
      setStyle('defHigh')
    }
  }, [children])
  return (
    <div className={`${styles[style]} ${styles.basicOval}`}>
      {children}
    </div>
  )
}
