import { useState, useEffect } from 'react'
// scss
import styles from 'components/Input/InputBox.module.scss'

export default function InputBox({
  label,
  type,
  value,
  onChange,
  maxLength,
  setIsError
}) {
  const [errorMessage, setErrorMessage] = useState('')

  // password
  const checkPassword = () => {
    if (value.length === 20) {
      setErrorMessage('20')
    } else if (value.includes(' ')) {
      setErrorMessage('')
    } else {
      setErrorMessage('')
    }
  }

  // name
  const checkName = () => {
    const whitespaceRegex = /^\s*$/

    if (value.length === 30) {
      setErrorMessage('30')
    } else if (whitespaceRegex.test(value) && value.length !== 0) {
      setErrorMessage('')
    } else {
      setErrorMessage('')
    }
  }

  // description
  const checkDescription = () => {
    const whitespaceRegex = /^\s*$/

    if (value.length === 80) {
      setErrorMessage('80')
    } else if (whitespaceRegex.test(value) && value.length !== 0) {
      setErrorMessage('')
    } else {
      setErrorMessage('')
    }
  }

  // email
  const checkEmail = () => {
    const whitespaceRegex = /^\s*$/

    if (value.length === 100) {
      setErrorMessage('100')
    } else if (whitespaceRegex.test(value) && value.length !== 0) {
      setErrorMessage('')
    } else {
      setErrorMessage('')
    }
  }

  useEffect(() => {
    if (label === '' || label === '') {
      checkPassword()
    } else if (label === '') {
      checkName()
    } else if (label === '') {
      checkDescription()
    } else if (label === 'Email') {
      checkEmail()
    }
  }, [value, label])

  useEffect(() => {
    if (errorMessage !== '') {
      setIsError(true)
    } else {
      setIsError(false)
    }
  }, [errorMessage])

  return (
    <div className={styles.inputBoxContainer}>
      <label
        className={`${styles.inputLabel} ${value !== '' && styles.active}`}
        htmlFor={label}
      >
        {label}
      </label>
      <input
        className={`${styles.inputBox} ${errorMessage !== '' && 'danger'}`}
        id={label}
        type={type || 'text'}
        value={value || ''}
        onChange={(e) => onChange?.(e.target.value)}
        maxLength={maxLength || ''}
      />
      <span className={styles.error}>{errorMessage}</span>
    </div>
  )
}
