import { useLocation, useNavigate, useParams } from 'react-router-dom'
import { useEffect, useRef, useState } from 'react'
import Toast from 'utils/sweetAlertConfig.js'

// scss
import styles from 'pages/EditReviewPage/EditReviewPage.module.scss'

// components
import MainLayout from 'components/MainLayout/MainLayout'
import WholePageModal from 'components/Modal/WholePageModal'
import IconRadioInput from 'components/Input/IconRadioInput'
import Button from 'components/Button/Button'
import ImgDropzone from 'components/Input/ImgDropzone'
import UpLoadingSpinner from 'components/Spinner/UpLoadingSpinner'
// api
import { editPost, getPostForEdit, publishPost, scratchPost } from 'api/post'

export default function EditReviewPage() {
  const location = useLocation()
  const navigate = useNavigate()
  const { reviewID } = useParams()
  const [isNewPost, setIsNewPost] = useState(null)
  const [isDraft, setIsDraft] = useState(null)
  const [isLoading, setIsLoading] = useState('')
  const formRef = useRef(null)
  // state
  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [difficulty, setDifficulty] = useState(null)
  const [recommend, setRecommend] = useState(null)
  const [uploadedImage, setUploadedImage] = useState(null)
  const [image, setImage] = useState(null)
  const [category, setCategory] = useState(null)

  // 
  const handleDifficultyChange = (number) => {
    setDifficulty(number)
  }

  const handleRecommendChange = (number) => {
    setRecommend(number)
  }

  // 
  const handleDrop = (acceptedFiles) => {
    // 
    const file = acceptedFiles[0]
    const reader = new FileReader()
    reader.onloadend = () => {
      setUploadedImage(reader.result)
    }
    reader.readAsDataURL(file)
    setImage(file)
  }

  // 
  const isNotValid =
    /^\s*$/.test(title) ||
    !category ||
    !difficulty ||
    !recommend ||
    !uploadedImage ||
    /^\s*$/.test(description)

  const postData = {
    title,
    category,
    description,
    image,
    difficulty,
    recommend
  }

  // 
  const handleFormSubmit = async (event) => {
    event.preventDefault()
    // 
    if (isNotValid) {
      Toast.fire({
        icon: 'info',
        title: '!'
      })
    } else {
      setIsLoading('')

      //   call API
      if (isNewPost) {
        // 
        const { postId } = await publishPost(postData)
        if (postId) {
          setIsLoading('')
          Toast.fire({
            icon: 'success',
            title: '!'
          })
          navigate(`/review/${postId}`)
        } else if (!postId) {
          setIsLoading('')
        }
      } else if (!isNewPost) {
        // 
        const { success } = await editPost({
          reviewID,
          ...postData,
          inProgress: false
        })
        if (success) {
          setIsLoading('')
          Toast.fire({
            icon: 'success',
            title: '!'
          })
          navigate(`/review/${reviewID}`)
        } else if (!success) {
          setIsLoading('')
        }
      }
    }
  }

  // 
  const handleEditScratch = async (event) => {
    event.preventDefault()
    // 
    if (isNotValid) {
      Toast.fire({
        icon: 'info',
        title: '!'
      })
    } else {
      setIsLoading('')
      const { success } = await editPost({
        reviewID,
        ...postData,
        inProgress: true
      })
      if (success) {
        setIsLoading('')
        Toast.fire({
          icon: 'success',
          title: '!'
        })
        navigate(`/review/${reviewID}`)
      } else if (!success) {
        setIsLoading('')
      }
    }
  }

  // 
  const handleScratch = async (event) => {
    event.preventDefault()
    if (isNotValid) {
      Toast.fire({
        icon: 'info',
        title: '!'
      })
    } else {
      setIsLoading('')
      const currentUserId = localStorage.getItem('currentUserId')
      const { postId } = await scratchPost(postData)
      if (postId) {
        setIsLoading('')
        Toast.fire({
          icon: 'success',
          title: '!'
        })
        navigate(`/user/${currentUserId}/myReviews`)
      } else if (!postId) {
        setIsLoading('')
      }
    }
  }

  useEffect(() => {
    const editOrNew = async () => {
      const token = localStorage.getItem('token')
      if (!token) {
        navigate('/login')
      }
      if (location.pathname === '/newReview') {
        setIsNewPost(true)
      } else {
        setIsNewPost(false)
        setIsLoading('')
        const { postData } = await getPostForEdit(reviewID)
        if (postData) {
          if (postData.inProgress === false) {
            setIsDraft(false)
          } else if (postData.inProgress === true) {
            setIsDraft(true)
          }
          setTitle(postData.title)
          setDescription(postData.description)
          setUploadedImage(postData.image)
          setDifficulty(postData.difficulty)
          setRecommend(postData.recommend)
          setCategory(postData.category)
          setIsLoading('')
        } else {
          Toast.fire({
            icon: 'error',
            title: '!'
          })
          setIsLoading('')
          navigate('/search/allReviews')
        }
      }
    }
    editOrNew()
  }, [location])

  return (
    <div>
      {isLoading !== '' && <UpLoadingSpinner text={isLoading} />}
      <MainLayout>
        <WholePageModal>
          <h3 className={styles.title}>
            {isNewPost ? '' : isDraft ? '' : ''}
          </h3>
          <form className={styles.form} ref={formRef}>
            {/*  */}
            <label htmlFor="title" className={styles.inputLabel}>
              
            </label>
            <input
              type="text"
              maxLength="40"
              id="title"
              name="title"
              value={title}
              placeholder=""
              onChange={(e) => setTitle(e.target.value)}
            />
            <div className={styles.titleLength}>{title.length} / 40</div>
            {/*  */}
            <label className={styles.inputLabel}></label>
            <div>
              <input
                type="radio"
                id="baiyue"
                name="category"
                value=""
                checked={category === ''}
                readOnly
              />
              <label htmlFor="baiyue" onClick={(e) => setCategory('')}>
                
              </label>
              <input
                type="radio"
                id="subur"
                name="category"
                value=""
                checked={category === ''}
                readOnly
              />
              <label htmlFor="subur" onClick={(e) => setCategory('')}>
                
              </label>
              <input
                type="radio"
                id="oversea"
                name="category"
                value=""
                checked={category === ''}
                readOnly
              />
              <label htmlFor="oversea" onClick={(e) => setCategory('')}>
                
              </label>
            </div>
            {/*  */}
            <label className={styles.inputLabel}></label>
            <IconRadioInput
              iconType="difficulty"
              onChange={handleDifficultyChange}
              score={difficulty}
            />
            {/*  */}
            <label className={styles.inputLabel}></label>
            <IconRadioInput
              iconType="recommend"
              onChange={handleRecommendChange}
              score={recommend}
            />
            {/*  */}
            <label className={styles.inputLabel}></label>
            <ImgDropzone uploadedImage={uploadedImage} onDrop={handleDrop} />
            {/*  */}
            <label className={styles.inputLabel}></label>
            <textarea
              maxLength="1000"
              placeholder=""
              name="description"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
            />
            <div className={styles.descriptionLength}>
              {description.length} / 1000
            </div>
            {/*  */}
            <div className={styles.buttonGroup}>
              {isNewPost || isDraft ? (
                <>
                  <div className={styles.buttonContainer}>
                    <Button
                      style={
                        isNewPost ? 'secondaryButtonLight' : 'secondaryButton'
                      }
                      text={isNewPost ? '' : ''}
                      onClick={isNewPost ? handleScratch : handleEditScratch}
                    />
                  </div>
                  <div className={styles.buttonContainer}>
                    <Button
                      style="secondaryButton"
                      text={isNewPost ? '' : ''}
                      onClick={handleFormSubmit}
                    />
                  </div>
                </>
              ) : (
                <div className={styles.buttonContainer}>
                  <Button
                    style="secondaryButton"
                    text=""
                    onClick={handleFormSubmit}
                  />
                </div>
              )}
            </div>
          </form>
        </WholePageModal>
      </MainLayout>
    </div>
  )
}
