import { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import Toast from 'utils/sweetAlertConfig.js'
// scss
import styles from 'components/Modal/SignUpModal.module.scss'

// components
import Button from 'components/Button/Button'
import InputBox from 'components/Input/InputBox'

// svg
// import googleLogo from 'assets/icons/google-logo.svg'
// import facebookLogo from 'assets/icons/facebook-logo.svg'

// api
import { signUp, login } from 'api/auth.js'

export default function SignUpModal() {
  const [name, setName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [passwordCheck, setPasswordCheck] = useState('')
  const [isError, setIsError] = useState('')
  const navigate = useNavigate()

  // 
  const isNotValid =
    name.trim().length === 0 ||
    password.trim().length === 0 ||
    passwordCheck.trim().length === 0 ||
    email.trim().length === 0

  const emailRegex = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/

  // 
  const handleSignUp = async () => {
    // 
    if (password !== passwordCheck) {
      Toast.fire({
        icon: 'warning',
        title: '!'
      })
      return
    } else if (isError || isNotValid) {
      Toast.fire({
        icon: 'warning',
        title: '!'
      })
      return
    } else if (!emailRegex.test(email)) {
      Toast.fire({
        icon: 'warning',
        title: 'email!'
      })
      return
    }

    const { success, message } = await signUp({
      name,
      email,
      password,
      passwordCheck
    })
    if (success === true) {
      Toast.fire({
        icon: 'success',
        title: '!'
      })
      // 
      const { token, id, avatar } = await login({
        email,
        password
      })
      if (token) {
        localStorage.setItem('token', token)
        localStorage.setItem('currentUserId', id)
        localStorage.setItem('currentUserAvatar', avatar)
        navigate('/')
      } else if (!token) {
        navigate('/login')
      }
    } else if (message === 'Error: Email already exists!') {
      Toast.fire({
        icon: 'warning',
        title: 'Email!'
      })
    }
  }
  return (
    <div className={styles.modalContainer}>
      <h2></h2>
      <div className={styles.inputGroup}>
        <InputBox
          label=""
          value={name}
          maxLength="30"
          onChange={(inputValue) => setName(inputValue)}
          setIsError={setIsError}
        />
        <InputBox
          label="Email"
          value={email}
          maxLength="100"
          onChange={(inputValue) => setEmail(inputValue)}
          setIsError={setIsError}
        />
        <InputBox
          label=""
          type="password"
          value={password}
          maxLength="20"
          onChange={(inputValue) => setPassword(inputValue)}
          setIsError={setIsError}
        />
        <InputBox
          label=""
          type="password"
          value={passwordCheck}
          onChange={(inputValue) => setPasswordCheck(inputValue)}
          setIsError={setIsError}
        />
        <Button
          style="primaryButtonBright"
          text=""
          onClick={handleSignUp}
        />
      </div>
      <div className={styles.text}>
        ?
        <Link className={styles.links} to="/login">
          
        </Link>
      </div>
      {/* <div className={styles.buttonContainer}>
        <Button style="primaryButtonWhite" text="google" />
        <img className={styles.logo} src={googleLogo} alt={googleLogo} />
      </div>
      <div className={styles.buttonContainer}>
        <Button style="primaryButtonWhite" text="facebook" />
        <img className={styles.logo} src={facebookLogo} alt={facebookLogo} />
      </div> */}
    </div>
  )
}
